  let searchTerm;
  let searching = true;

  let searchResultsObj = [];

  function searchCities() {
    searching = true;
    searchResultsObj = [];
    states.forEach((state) => {
      let resultObj = state.cities.filter((city) => {
        return city.city.toLowerCase().includes(searchTerm?.toLowerCase());
      });
      if (resultObj.length !== 0) searchResultsObj.push(resultObj);
    });
  }

  let selectedCity;

  function getSelectedCity(city) {
    selectedCity = city;
    searching = false;
  }

  function getCityDetails(e) {}

  $: selectedForm = $formsData.filter((form) => form.active === true);

  function showFormOptions(form) {
    selectedForm[0].active = false;
    $formsData = $formsData;
  }
</script>

<section class="container">
  <div class="titleWrapper">
    <h1>Submit request</h1>
  </div>
  {#if selectedForm.length <= 0}
    <section class="selectForm" in:slide>
      <h2>Please choose a pursuit category:</h2>
      <div class="formChoiceContainer">
        {#each $formsData as formData}
          <div
            class="formChoiceWrapper"
            class:formChoiceHovered={formData.hovered === true}
            on:mouseover={() => (formData.hovered = true)}
            on:focus={() => (formData.hovered = true)}
            on:mouseout={() => (formData.hovered = false)}
            on:blur={() => (formData.hovered = false)}
            on:click={() => {
              formData.active = true;
              formData.hovered = false;
            }}
            in:fly={{ y: -30, duration: 300 }}
          >
            {#if formData.img === "large"}
              <div class="formChoiceImg largePlant">
                <SmallPlant />
              </div>
            {:else}
              <div class="formChoiceImg smallPlant">
                <LargePlant />
              </div>
            {/if}

            <div class="formChoice">
              {formData.name}
            </div>
          </div>
        {/each}
      </div>
    </section>
  {:else}
    <form action="" class="form" transition:slide>
      <div class="formTitleWrapper">
        <div class="backButton" on:click={showFormOptions}>
          <BackButton
            iconBackgroundColor="#fff"
            iconColor="white"
            itemIconOpacity="0"
            iconStrokeWidth="3rem"
            back="true"
          />
        </div>
        <h2>
          {selectedForm[0].name} pursuit request
        </h2>
      </div>

      <fieldset class="formSectionsContainer">
        {#each selectedForm[0].sections as section}
          <section class="formSection">
            <h3>
              {section.name}
            </h3>
            <div class="formSectionContent">
              {#each section.questions as question}
                {#if question.type === "text"}
                  <div class="questionContainer">
                    <label for={question.question}>{question.question}</label>
                    <input class="text" id={question.question} type="text" bind:value={question.response} />
                  </div>
                {:else if question.type === "textarea"}
                  <div class="questionContainer">
                    <label for={question.question}>{question.question}</label>
                    <textarea class="text" id={question.question} type="text" bind:value={question.response} />
                  </div>
                {:else if question.type === "offices"}
                  <div class="questionContainer">
                    <label for="office">Deloitte office supporting the pursuit (for the letterhead)*</label>
                    <div class="office" class:officeSelected={!searching}>
                      {#if searching}
                        <input id="office" type="text" bind:value={searchTerm} on:keyup={searchCities} />
                        {#if searchTerm !== ""}
                          <ul>
                            {#each searchResultsObj as result}
                              {#each result as resultCity}
                                <li class="resultOffice" on:click={() => getSelectedCity(resultCity)}>
                                  <span>{resultCity.city}</span>, {resultCity.address}
                                </li>
                              {/each}
                            {/each}
                          </ul>
                        {/if}
                      {/if}

                      {#if selectedCity && searching === false}
                        <div class="selectedCityWrapper">
                          <div class="selectedCityDetails">
                            <div class="city">{selectedCity.city}</div>
                            <div>{selectedCity.address}</div>
                            <div class="phone"><span>Phone:</span> {selectedCity.phone}</div>
                            <div class="fax"><span>Fax:</span> {selectedCity.fax}</div>
                          </div>
                          <div class="changeIcon" on:click={() => (searching = true)}>Change</div>
                        </div>
                      {/if}
                    </div>
                  </div>
                {:else if question.type === "choice"}
                  <div class="questionContainer">
                    <label for={question.question}>{question.question}</label>
                    <div class="optionsWrapper">
                      <fieldset class="options" id={question.question}>
                        {#each question.choices as choice}
                          <div class="option">
                            <input
                              label={choice.text}
                              id={choice.text}
                              type="radio"
                              value={choice.text}
                              bind:group={question.response}
                            />
                            <!-- <label class="hidden" for={choice.id}> {choice?.text || ""}</label> -->
                          </div>
                        {/each}
                      </fieldset>
                    </div>
                  </div>
                {:else if question.type === "multiple text"}
                  <div class="questionContainer">
                    <label for={question.question}>{question.question}</label>
                    <div class="optionsWrapper">
                      <fieldset
                        class="options multipleText"
                        class:multipleTextRole={question.group === true}
                        id={question.question}
                      >
                        {#each question.choices as choice, idx}
                          <div class="option textOption">
                            <label class="hidden" for={choice.id}> {choice?.text || ""} </label>
                            <input id={choice.id} type="text" bind:value={choice.response} />
                            {#if choice?.role}
                              <label class="hidden" for={choice.id}> {choice?.role || ""}</label>
                              <input id={choice.id} type="text" bind:value={choice.roleResponse} />
                            {/if}
                          </div>
                        {/each}
                      </fieldset>
                    </div>
                  </div>
                {:else if question.type === "multiple textarea"}
                  <div class="questionContainer">
                    <label for={question.question}>{question.question}</label>
                    <div class="optionsWrapper">
                      <fieldset class="options" id={question.question}>
                        {#each question.choices as choice}
                          <div class="option textOption">
                            <label class="hidden" for={choice.id}> {choice?.text || ""}</label>
                            <textarea label={choice.text} id={choice.id} type="text" bind:value={choice.response} />
                          </div>
                        {/each}
                      </fieldset>
                    </div>
                  </div>
                {/if}
              {/each}
            </div>
          </section>
        {/each}
      </fieldset>
    </form>
  {/if}
</section>

