<script>
  //ICONS
  import BackButton from "../Icons/Back.svelte";

  //COMPONENTS
  import CalendarIcon from "../Elements/Calendar Icon.svelte";
  import { crossfade, fade } from "svelte/transition";
  const [send, receive] = crossfade({});
  import { users, projects } from "../store.js";
  import { range, getDaysArray, firstDayOfWeek, addDays, subtractDays } from "../helper.js";
  import { slide, fly } from "svelte/transition";
  import { quintInOut } from "svelte/easing";

  let lastMonday = firstDayOfWeek(new Date(), 1);

  let weekDates = getDaysArray(lastMonday, addDays(lastMonday, 6));
  let weekDays = weekDates.map((date) => date.toLocaleString("en-us", { weekday: "short" }));

  let standardGridAreas = ["event1", "event2", "event3", "event4", "event5", "event6", "event7"].join(" ");

  function getGridAreas(project) {
    let gridAreas = weekDates.map((date) =>
      date
        .toLocaleString("en-us", { month: "short", day: "numeric", year: "numeric" })
        .replace(",", "")
        .replaceAll(" ", "")
    );

    if (new Date(project.startDate) < lastMonday) {
      let projectDaysArray = getDaysArray(new Date(project.startDate), new Date(project.endDate));
      let midStartDay;
      let midStartDayIdx;
      projectDaysArray.forEach((projectDay, idx) => {
        if (projectDay.getDate() == lastMonday.getDate()) {
          // console.log("Falling on Monday " + project.projectName + " " + projectDay);
          midStartDay = projectDay;
          midStartDayIdx = idx;
        }
      });
      let startDay = new Date(midStartDay)
        .toLocaleString("en-us", { month: "short", day: "numeric", year: "numeric" })
        .replace(",", "")
        .replaceAll(" ", "");
      let endDay = new Date(project.endDate)
        .toLocaleString("en-us", { month: "short", day: "numeric", year: "numeric" })
        .replace(",", "")
        .replaceAll(" ", "");
      let numberOfDays = projectDaysArray.length - midStartDayIdx;
      let eventAreas = "";
      for (let i = 0; i < numberOfDays; i++) {
        eventAreas += "event ";
      }
      gridAreas.splice(gridAreas.indexOf(startDay), numberOfDays, eventAreas);
      project.gridareas = gridAreas;
      // console.log(gridAreas);
      return gridAreas.join(" ");
    } else {
      let startDay = new Date(project.startDate)
        .toLocaleString("en-us", { month: "short", day: "numeric", year: "numeric" })
        .replace(",", "")
        .replaceAll(" ", "");
      let endDay = new Date(project.endDate)
        .toLocaleString("en-us", { month: "short", day: "numeric", year: "numeric" })
        .replace(",", "")
        .replaceAll(" ", "");
      let numberOfDays = project.duration;
      let eventAreas = "";
      for (let i = 0; i < numberOfDays; i++) {
        eventAreas += "event ";
      }
      gridAreas.splice(gridAreas.indexOf(startDay), numberOfDays, eventAreas);
      project.gridareas = gridAreas;
      // console.log(gridAreas);
      return gridAreas.join(" ");
    }
  }

  let movedEvent;

  function handleDragStart(e) {
    e.dataTransfer.setData("text/plain", e.target.dataset.projectId);
    // let itemToMove = $projects.find((proj) => proj.id == e.target.dataset.projectId);
    dragged = true;
    movedEvent = e.target;
    movedEvent.style.zIndex = 999;
  }

  function handleDragStop() {
    dragged = false;
  }

  function handleDrop(e) {
    let projectId = e.dataTransfer.getData("text/plain");
    let itemToMove = $projects.find((proj) => proj.id == projectId);
    let itemIndex = $projects.indexOf(itemToMove);
    itemToMove.assignedTo = e.currentTarget.dataset.toUser;
    let startDateBefore = new Date(itemToMove.startDate);

    itemToMove.startDate = new Date(e.currentTarget.dataset.newFromDate).toISOString();
    let newStartDate = new Date(e.currentTarget.dataset.newFromDate);
    let daysMoved = new Date(e.currentTarget.dataset.newFromDate).getDay() - startDateBefore.getDay();
    if (daysMoved !== 0) {
      newStartDate.setDate(newStartDate.getDate() + itemToMove.duration - 1);
      itemToMove.endDate = newStartDate.toISOString();
      console.log(itemToMove.endDate);
    }
    $projects = $projects;
    dragged = false;
    movedEvent.style.zIndex = "auto";
    console.log(itemToMove);
  }

  let currentWeekProjects;
  let sunday;
  $: if (lastMonday) {
    currentWeekProjects = $projects.filter((project) => {
      // console.log("MONDAY: " + lastMonday);
      sunday = new Date(lastMonday); //to set Sunday date to Monday
      sunday.setDate(lastMonday.getDate() + 6);
      let monday = new Date(lastMonday); //to set Monday date to one day before
      monday.setDate(lastMonday.getDate());
      // console.log("SUNDAY: " + sunday);

      return (
        (new Date(project.startDate) >= monday || new Date(project.endDate) >= monday) &&
        (new Date(project.endDate) <= sunday || new Date(project.startDate) <= sunday)
      );
    });
    $projects = $projects;
  }

  function handleClick(e) {}

  let dragged;

  function showNextWeek() {
    lastMonday.setDate(lastMonday.getDate() + 7);
    weekDates = getDaysArray(lastMonday, addDays(lastMonday, 6), 1);
    lastMonday = lastMonday;
    $projects = $projects;
  }

  function showPrevWeek() {
    lastMonday.setDate(lastMonday.getDate() - 7);
    weekDates = getDaysArray(lastMonday, addDays(lastMonday, 6), 1);
    lastMonday = lastMonday;
    $projects = $projects;
  }

  function showCalendar() {}

  let colors = [];

  $: unassignedProjects = $projects
    .filter((p) => p.assignedTo === "none")
    .sort((a, b) => (new Date(a.endDate) > new Date(b.endDate) ? 1 : -1));
</script>

<div class="deploymentContainer">
  <div class="deploymentTitle">
    <h1>Deployment</h1>
  </div>
  <div class="container">
    <div class="unassigned">
      <h2>Pursuit queue</h2>
      <div class="unassignedProjects">
        {#if unassignedProjects.length > 0}
          {#each unassignedProjects as project (project.id)}
            <div
              class="unassignedProject"
              draggable="true"
              data-project-id={project.id}
              on:dragstart={handleDragStart}
              on:dragend={handleDragStop}
              on:click={handleClick}
              in:receive={{ key: project, duration: 500 }}
              out:send={{ key: project, duration: 500 }}
            >
              <div
                class="unassignedProjectName"
                class:red={new Date(project.endDate) <= addDays(new Date(), 2)}
                class:orange={new Date(project.endDate) > addDays(new Date(), 2) &&
                  new Date(project.endDate) < addDays(new Date(), 4)}
                class:yellow={new Date(project.endDate) >= addDays(new Date(), 3)}
                class:green={new Date(project.endDate) >= addDays(new Date(), 5)}
                in:fly={{ y: -10 }}
              >
                {project.projectName}
              </div>
              <div class="unassignedProjectDetails" in:fly={{ y: 10 }}>
                <div class="unassignedProjectOwner">
                  <div class="unassignedProjectSubmitterWrapper">
                    <div class="unassignedProjectSubmitterPic">
                      <img src="https://americas.internal.deloitteonline.com/sites/pursuitscoe/dynamic/SitePages/images/champerera.jpg" alt="champerera" />
                    </div>
                    <div class="unassignedProjectSubmitter">
                      {project.submitter}
                    </div>
                  </div>
                </div>
                <div class="unassignedProjectBottom">
                  <div class="unassignedProjectValue">
                    ${project.value / 1000}K
                  </div>
                  <div class="unassignedProjectDate">
                    <span>Due:</span>
                    {new Date(project.endDate).toLocaleString("en-us", { day: "numeric", month: "short" })}
                  </div>
                </div>
              </div>
            </div>
          {/each}
        {:else}
          <p>No projects in queue.</p>
        {/if}
      </div>
    </div>
    <div class="scheduleHeader">
      <div class="schedulerNavMenu">
        <div class="prev" on:click={showPrevWeek}>
          <BackButton
            iconBackgroundColor="#fff"
            iconColor="white"
            itemIconOpacity="0"
            iconStrokeWidth="1rem"
            back="true"
          />
        </div>
        <div class="currentWeekEnding" on:click={showCalendar}>
          <span>Week ending</span>
          {sunday.toLocaleString("en-us", { day: "numeric", month: "long", year: "numeric" })}
        </div>
        <div class="next" on:click={showNextWeek}>
          <BackButton iconBackgroundColor="#fff" iconColor="white" itemIconOpacity="0" iconStrokeWidth="1rem" />
        </div>
      </div>
      <div class="headerRow">
        <div class="name nameHeader">Pursuit managers</div>
        <div class="daySegments">
          {#each weekDays as weekday, idx}
            <div class="segment">
              {weekday}
              <b>{String(weekDates[idx]).split(" ")[2]} </b>
            </div>
          {/each}
        </div>
      </div>
    </div>
    <div class="scheduleContainer">
      {#each $users as user, index (user.id)}
        <div class="row" in:fly={{ y: -20 }}>
          <div class="name">{user.name}</div>
          <div class="timelineWrapper">
            <div class="timeline">
              {#each currentWeekProjects.filter((p) => p.assignedTo === user.email) as project, index (project.id)}
                <div
                  class="daySegments"
                  style="display: grid; 
                grid-template-columns: repeat(7, 1fr); 
                grid-template-areas:'{getGridAreas(project)}'"
                >
                  <div
                    class="event"
                    draggable="true"
                    style="grid-area:event"
                    data-from-user={user.email}
                    data-project-id={project.id}
                    on:dragstart={handleDragStart}
                    on:click={handleClick}
                    in:receive={{ key: project, duration: 500 }}
                    out:send={{ key: project, duration: 500 }}
                    class:red={new Date(project.endDate) <= addDays(new Date(), 2)}
                    class:orange={new Date(project.endDate) > addDays(new Date(), 2) &&
                      new Date(project.endDate) < addDays(new Date(), 4)}
                    class:yellow={new Date(project.endDate) >= addDays(new Date(), 3)}
                    class:green={new Date(project.endDate) >= addDays(new Date(), 5)}
                  >
                    {#if new Date(project.startDate) < lastMonday}
                      ++ {project.projectName}<br />
                      <!-- {new Date(project.startDate).toLocaleString("en-us", { day: "numeric", month: "short" })}—{new Date(
                      project.endDate
                    ).toLocaleString("en-us", { day: "numeric", month: "short" })} -->
                    {:else if new Date(project.endDate) > sunday}
                      {project.projectName}++<br />
                      <!-- {new Date(project.startDate).toLocaleString("en-us", { day: "numeric", month: "short" })}—{new Date(
                      project.endDate
                    ).toLocaleString("en-us", { day: "numeric", month: "short" })} -->
                    {:else}
                      {project.projectName}<br />
                      <!-- {new Date(project.startDate).toLocaleString("en-us", { day: "numeric", month: "short" })}—{new Date(
                      project.endDate
                    ).toLocaleString("en-us", { day: "numeric", month: "short" })} -->
                    {/if}
                  </div>
                </div>
              {/each}
            </div>
            {#if dragged}
              <div class="timeline">
                <div class="daySegmentsBase" style="grid-template-areas:{standardGridAreas}">
                  {#each weekDates as date, idx}
                    <div
                      class="dropArea"
                      style="grid-area:'{'event' + idx}'"
                      data-to-user={user.email}
                      data-new-from-date={date}
                      on:drop={handleDrop}
                      on:dragenter={(e) => e.preventDefault()}
                      on:dragover={(e) => e.preventDefault()}
                    >
                      <!-- {date} -->
                    </div>
                  {/each}
                </div>
              </div>
            {/if}
          </div>
        </div>
      {/each}
    </div>
  </div>
</div>

